import { rem, colors } from "./styles";
import styled from 'styled-components';
import {centeredMix} from './paragraph';

export default styled.h2`
    font-size: ${props => props.size == 20 ? rem(36): rem(64)};                    
    display: block;                                       
    color: ${props => props.light ? colors.w : colors.d};
    margin-bottom: ${rem(26)};                
    max-width: 50rem; 
    font-weight: ${fontWeight};
    ${centeredMix};
`

export const fs = ({HtmlTag, children, size, centered, bold=true, light=false}) => {
    let fontSize;
    let classes = [ 'headline' ];
    if(centered) {
        classes.push('-centered');
    }
    let fontWeight = bold ? 700 : 400;
    

    return (
        <HtmlTag className={classes.join(' ')}>{children}
            <style jsx>{`
                .-centered {                    
                    margin-left: auto;
                    margin-right: auto;
                    text-align: center;
                }
            `}</style>
        </HtmlTag>
    )
}